import React from 'react'
// import axios from "axios";
// import { useState, useEffect } from "react";
import './DetailCard.css'
import { UilTimes } from "@iconscout/react-unicons";

const DateTime = (time) => {
    var date = time.split("T");
  
    return date[0];
  };

const DetailCard = (props) => {
    let param = props.card;
  return (
    <div className="detail-card">
        <div className="info">
            <span><span>M n hng:</span>{param.orderId}</span><br/>
            <span><span>Ngy to n:</span>{DateTime(param.createAt)}</span><br/>
            <span><span>Trng lng hng:</span>{param.weight}</span><br/>
            <span><span>a ch kho:</span>{param.warehouse.address.detail}</span><br/>
            <span><span>a ch nhn hng:</span>{param.receiver.address.detail}</span><br/>
            <span><span>Gi:</span>{param.cod}</span><br/>
            <span><span>Ph Ship:</span>{param.fee}</span><br/>
            <span><span>Tn ngi nhn: </span>{param.receiver.name}</span><br/>
            <span><span>S in thoi ngi nhn: </span>{param.receiver.phone}</span><br/>
        </div>
        <div style={{ alignSelf: "flex-end", cursor: "pointer", color: "black" }}>
            <UilTimes
                onClick={props.setDetail}
            />
        </div>
    </div>
  )
}

export default DetailCard